*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.main-div{
    background-color: blue;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.child-div{
    width: 30%;
    height: 70vh;
    background-color: aqua;
    border-radius: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

figure img{
    width: 70px;
    height: 70px;
    border-radius: 20px;
    margin-left: 30px;
    margin-top: 10px;
}

figure figcaption{
    font-size: 18px;
}

.addItem input{
    border-radius: 5px;
    width: 270px;
    height: 28px;
    font-size: 20px;
}

.addItem .add{
    width: 30px;
    height: 30px;
    margin-bottom: 7px;
}

.addItem .edit{
    width: 22px;
    height: 22px;
    margin-top: 2px;
}

.eachItem {
    width: 270px;
    height: 28px;
    display: flex;
    justify-content: space-between;
    background-color: antiquewhite;
    border-radius: 5px;
    margin: 20px 0px;
    margin-right: 20px;

}

.eachItem h3{
    font-size: 20px;

}

.todo-btn .delete{
    width: 22px;
    height: 22px;
    margin-top: 2px;
}

.todo-btn .edit{
    width: 22px;
    height: 22px;
    margin-top: 2px;

}
.showItem .btn {
    margin-top: 10px;
    background-color: red;
    width: 100px;
    height: 30px;
    font-size: 15px;
}
 
